<template>
  <div style="padding:20px;">
    <Form ref="sales" :model="sales" :rules="rules" label-position="left" :label-width="120">
      <FormItem label="活动类型">
        <Select v-model="sales.itemmodel"  disabled class="formItem">
          <Option  value='2' >秒杀商品</Option>
        </Select>
      </FormItem>
      <FormItem label="活动商品">
        <Input v-model="itemName" disabled class="formItem"></Input>
      </FormItem>
      <FormItem label="活动价格" prop="price">
        <InputNumber v-model='sales.price' placeholder='请输入活动价格' :min="0" class="formItem"></InputNumber>
        <span style='color:#ccc;margin-left:20px;'>此价格为活动价，如果恢复成普通商品,价格也将恢复成出售价格。</span>
      </FormItem>
      <!-- <FormItem label="活动数量" prop="num" >
        <InputNumber v-model='sales.num'  placeholder='请输入活动数量' :min="0" class="formItem" :precision="0"></InputNumber>
      </FormItem> -->
      <FormItem label="活动时间" prop="date">
        <DatePicker type="datetimerange" placement="bottom-end" v-model="sales.date"  split-panels class="formItem"></DatePicker>
        <span style='color:#ccc;margin-left:20px;'>如果时间过期，该商品将直接变成普通商品。</span>
      </FormItem>
      <FormItem label="原始库存">
        <Input v-model='originalNum' disabled class="formItem"></Input>
      </FormItem>
      <FormItem>
        <Button type='primary' @click="handleSave('sales')">
          保存编辑
        </Button>
        <Button @click="cancel">取消</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
  export default {
    data() {
      const validatePrice=(r,v,callback)=>{
        if(v===''||v===null){
          callback(new Error('请输入金额'));
        }{
          callback();
        }
      };
      const validateNum=(r,v,callback)=>{
        if(v===''||v===null){
          callback(new Error('请输入数量'));
        }else if(v>this.originalNum){
          callback(new Error('活动数量不能大于库存'));
        }else{
          callback();
        }
      };
      const validateDate=(r,v,callback)=>{
        if(v[0]===''||v[1]===''){
          callback(new Error('请选择日期'));
        }else{
          callback();
        }
      };
      return {
        sales: {
          itemmodel: '2',
          itemid: '',
          // num: 0,
          price: 0,
          ishidden:0,
          date:[new Date(),new Date()]
        },
        itemName:'',
        originalNum:0,
        rules: {
          price: [
            { validator: validatePrice, trigger: 'blur' }
          ],
          // num: [
          //   { validator: validateNum, trigger: 'blur' }
          // ],
          date:[
            { validator: validateDate, trigger: 'blur' }
          ]
        },
      };
    },
    mounted() {
      this.$sitemap.prvname = "营销活动";
      this.$sitemap.currname = '编辑秒杀商品';
      this.getItem();
    },
    methods: {
      //获取item信息
      getItem(){
        let that=this;
        let data={ itemid: this.$route.query.id };
        that.$http.post(this.$api.item_detail,data).then(res => {
          let resData = res.data.data;
          if(res.data.result=='1'&&resData) {
            that.setItem(resData.item);
          }else{
            that.$Message.error(res.data.msg);
          }
        }).catch(err => {
          that.$Message.error('服务器异常');
        });
      },
      //表单赋值
      setItem(item) {
        this.sales.price = item.price;
        // this.sales.num = item.num;
        this.sales.itemid=item.id;
        this.sales.date=[item.seckillbegin,item.seckillend];
        this.originalNum = item.num;
        this.itemName=item.title;
      },
      //格式化数据
      formatData(){
        let params={};
        let that=this;
        Object.keys(that.sales).forEach(key => {
          switch (key){
            case 'date':
              params.seckillbegin =Date.parse(that.sales[key][0])/1000;
              params.seckillend	 =Date.parse(that.sales[key][1])/1000;
              break;
            default:
              params[key] = that.sales[key];
              break;
          }
        });
        return params;
      },
      //创建提交
      handleSave(name) {
        let that=this;
        that.$refs[name].validate((valid) => {
          if(valid){
            this.$http.post(this.$api.item_editmodel, that.formatData()).then(res => {
              if(res.data.result==1){
                that.$Message.success('编辑成功');
                that.$router.push('/seckilling/list');
              }else{
                that.$Message.error(res.data.msg);
              }
            }).catch(err => {
              that.$Message.error('服务器异常');
            })
          }else{
            that.$Message.error('请核对信息');
          }
        });
      },
      cancel(){
        this.$router.push('/seckilling/list');
      }
    },
  }
</script>
<style>
  .formItem{
    width: 350px;
  }
</style>



